import { Injectable } from '@angular/core';
import * as firebase from './fb';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CodeService {

  constructor() { }

  public getUserCodes(email) : Observable<any>
  {
    let codeObserver = new Observable(watcher => {
      let db = firebase.default.firestore();
      let codes = db.collection('code_data');
  
      let codeArray = [];
      codes.where('owner', '==', email).onSnapshot(snapshot => {
        snapshot.docChanges().forEach(c => {
          if(c.type == 'added')
          {
            const codeData = c.doc.data();
            codeArray.push(codeData);
          }
        });
        watcher.next(codeArray);
        watcher.complete();
      });
    })

    return codeObserver;
  }
}
